.taskslist {
  background-color: #1e293b;
  list-style-type: none;
  margin: 16px 0;
  border-radius: 4px;
  overflow: hidden;
  box-shadow: 0 0 14px rgba(0, 0, 0, 0.14);
}

.emptyList {
  margin: auto;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  color: #94a3b8;
}

.emptyList .empty {
  margin-bottom: 32px;
}
